.moon-map-grid {
  background-color: #333;
  border-radius: 8px;
  grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
  gap: 15px;
  padding: 10px;
  display: grid;
}

.moon-area {
  aspect-ratio: 1;
  text-align: center;
  color: #fff;
  cursor: pointer;
  box-sizing: border-box;
  background-color: #555;
  background-position: center;
  background-size: cover;
  border: 2px solid #777;
  border-radius: 8px;
  justify-content: center;
  align-items: center;
  padding: 5px;
  font-size: .9em;
  font-weight: bold;
  transition: transform .2s, box-shadow .2s;
  display: flex;
  overflow: hidden;
}

.moon-area:hover {
  border-color: #fff;
  transform: scale(1.05);
  box-shadow: 0 0 15px #ffffff4d;
}

.moon-area.owned {
  cursor: not-allowed;
  opacity: .7;
  background-color: #2a652a;
  border-color: #4caf50;
}

.moon-area.owned:hover {
  box-shadow: none;
  transform: none;
}
